import { useContext } from "react";
import { Cart } from "../Context";
import Cartlist from "./Cartlist";
import "./Cartpage.css";
import Navbar from "../Navbar/Navbar";
import HomeIcon from "@mui/icons-material/Home";
import { Link } from "react-router-dom";

const Cartpage = () => {
  const { cart } = useContext(Cart);

  return (
    <>
      <div className="cart-parent">
        <Navbar />
        <div className="cart-cont1">
          <br></br>
          <p>Cart</p>
          <br></br>
          <div className="cart-cont">
            {
              cart.map((items, cartindex) => (<Cartlist items={items} cartindex={cartindex} key={items.id} />))
            }
          </div>
        </div>
        <div className="cart-foot">
          <div className="cart-foot1">
            <div>
              <Link to="/invoice">
                <button className="porder" onClick={() => alert("Order has been Placed")}>Place Order</button>
              </Link>
            </div>
            <div className="cart-tot">
              <h5>Total</h5><span>₹ <p>
                {
                  cart && cart.map(items => items.price * items.quantity).reduce((total, value) => total + value, 0)
                }
              </p></span>
            </div>
          </div>
        </div>
      </div>
      <div className="c-home-icon1">
        <div className="c-home-icon">
          <Link to="/">
            <HomeIcon className="c-home" />
          </Link>
        </div>
      </div>
    </>
  );
};

export default Cartpage;
